body {
    background: white;
}

main {
    padding-top: 25px;
}

.ui.menu {
    background: #02244a;
}
.ui.menu .header.item:hover {
    color: white;
}

.ui.menu a.header.title.item {
    color: white;
}

.ui.menu .header.title.item:hover {
    color: #c7eef5;
}

.incroyablelibraire {
    position: fixed;
    left: 50%;
    top: 22px;
    z-index: 200;
    width: 300px;
    margin-left: -150px;
}

.incroyablelibraire .ui.tiny.images img {
    height: 80px;
}

.ui.piled.segment.introduction {
    background: #f07c41;
    color: white;
    border: 1px solid #df622c;
    margin: 2em 0;

    a {
        color: white;
        border-bottom: 1px dotted white;
    }
    a:hover {
        color: white;
        border-bottom: 1px solid white;
    }
}

.ui.segment.introduction .ui.header {
    color: white;
}

.ui.piled.segment.introduction:after, .ui.piled.segment.introduction:before,
.ui.stacked.segment.introduction:after, .ui.stacked.segment.introduction:before {
    background: #f07c41;
    border: 1px solid #df622c;
}

.ui.menu .item, .ui.inverted.link.list a.item {
    color: #c7eef5;
}

.ui.inverted.list>a.item>i.icon {
    color: inherit;
}

.ui.divided.inverted.horizontal.list>.item, .ui.divided.inverted.list>.item, .ui.divided.inverted.list>.list {
    border-color: #c7eef5;
}

.ui.menu .item img.logo {
    margin-right: 1.5em;
}
.main.container {
    margin-top: 6em;
}

.ui.text.container {
    max-width: 910px;
}

.wireframe {
    margin-top: 2em;
}
.ui.footer.segment {
    margin: 5em 0em 0em;
    padding: 5em 0em;
    background: #02244a;
    color: #c7eef5;
}

.cursive {
    font-family: 'Alex Brush', cursive !important;
    font-size: 2.5rem !important;
}

.cursive.small {
    font-size: 1.5rem !important;
}

h1, h2 {
    color: #df622c;
}

h2 a {
    color: #df622c;
}

h2 a:hover {
    color: #02244a;
}

hr {
    margin: 0;
    margin-bottom: 70px;
    border: 0;
    text-align: center;
}

hr:before {
    content: "\2022 \2022 \2022";
    font-size: 50px;
    color: #02244a;
}

.ui.footer.segment .cursive {
    color: white;
}

.footer hr {
    margin: 32px 0;
    margin-bottom: 48px;
}

.footer hr:before {
    color: white;
}

.ui.cards.remerciements > .card > .image {
    text-align: center;
    padding: 20px;
    background: #c7eef5;
    color: #02244a;
}

.ui.image.raised {
    //box-shadow: 0 0 0 1px #d4d4d5, 0 2px 4px 0 rgb(34 36 38 / 12%), 0 2px 10px 0 rgb(34 36 38 / 15%);
}

.author {
    margin-top: -8px;
    margin-bottom: 18px;
    color: #4183c4;
    font-weight: bold;
    font-style: italic;
}

.ui.dots.labels .label, .ui.dots.label {
    background-color: #c7eef5;
    border-color: #ccc;
    color: #02244a;
}

/* Mobile */

@media only screen and (max-width: 767px) {
    [class*="mobile hidden"],
    [class*="tablet only"]:not(.mobile),
    [class*="computer only"]:not(.mobile),
    [class*="large monitor only"]:not(.mobile),
    [class*="widescreen monitor only"]:not(.mobile),
    [class*="or lower hidden"] {
      display: none !important;
    }
}